.nav {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;
}

.nav_black {
    transition-timing-function: ease-in-out;
    background-color: rgb(12, 12, 12);
    transition: all 0.5s;
}

.nav_logo {
    width: 100px;
    object-fit: contain;
}

.nav_avatar {
    position: fixed;
    top: 15px;
    right: 20px;
    width: 40px;
    object-fit: contain;
}
