.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    margin-bottom: 20px;
}

.banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    text-shadow: rgb(44, 44, 44) 1px 0 10px;
}

.banner_desc {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    max-width: 360px;
    height: 80px;
    font-weight: 500;
    text-shadow: rgb(44, 44, 44) 1px 0 10px;
}

.banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    background-color: rgba(51, 51, 51, 0.7);
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

.banner_button:hover {
    color: black;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner_fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}

.banner_trailer {
    position: absolute;
    right: 250px;
    top: 10px;
    width: 600px;
}
