.row {
    color: white;
    margin-left: 20px;
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_poster {
    object-fit: contain;
    max-width: 250px;
    max-height: 100%;
    margin-right: 10px;
    transition: transform 450ms;
}

.row_poster:hover {
    transform: scale(1.08);
}

.row_posterLarge {
    max-height: 100%;
    max-width: 200px;
}

.row_posterlarge:hover {
    transform: scale(1.09);
}

.row_title {
    color: white;
    font-size: 1.1rem;
    width: 100%;
    font-weight: 600;
    text-align: center;
    margin-top: 10px;
}

.row_summary {
    background-color: rgba(39, 38, 38, 0.459);
    max-width: 250px;
    line-height: 1;
    bottom: 63px;
    position: absolute;
    opacity: 0;
    overflow: hidden;
}

.row_summary:hover {
    transition: all 1s;
    opacity: 1;
}

.row_contents {
    position: relative;
}

.row_summaryLarge {
    position: absolute;
    max-width: 200px;
    bottom: 62px;
    height: 115px;
    padding-top: 10px;
}
